<template>
    <div :class="`caption d-flex ${val ? 'green' : 'orange'}--text`">
        <v-icon small class="mx-1" :color="val ? 'green' : 'orange'">{{`mdi-${val ? 'check' : 'close'}-circle-outline`}}</v-icon>
        {{val ? "Yes" : "No"}}
    </div>
</template>
<script>
export default {
    name:'TableFieldPic',
    props: {
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    computed:{ 
        val () {
            return this.$getNestedValue(this.item, this.header.value)
        }
    }
}
</script>

<style lang="scss" scoped>
.caption {
    text-transform: capitalize;
}
</style>>
